.result {
    background: url(../img/bg-result.jpg) no-repeat;
    background-size: cover;
    background-position: center center;
    padding: 48px 0 35px;
    text-align: center;
    
    &_wrap {
        .row-flex();
        justify-content: center;
        text-align: left;
    }
    &_block {
        .col();
        .size(6);
        .size-md(8);
        .size-sm(10);
        .size-xs(12);
        padding: 45px 5px;
    
        h3 {
            font-size: 2rem;
            text-align: center;
        }
        ul {
            padding: 0 25px;
        }
        li {
            padding: 7px 0;
            padding-left: 70px;
            margin: 40px 0;
            position: relative;
        }
    }
    &_white {
        background-color: rgba(225,228,232,0.7);
        position: relative;
        &::after {
            top: 100%;
            left: 50%;
            border: solid transparent;
            content: " ";
            height: 0;
            width: 0;
            position: absolute;
            top: -48px;
            pointer-events: none;
            border-color: rgba(136, 183, 213, 0);
            border-top-color: #1a1b1b;
            border-width: 75px;
            margin-left: -75px;
        }
        &__item {
            &::before {
                content: '';
                position: absolute;
                left: 0; top: 0;
                background: url(../img/form.png) no-repeat;
                width: 56px;
                height: 56px;
            }
        }
    }
    &_black {
        background-color: rgba(20,20,20,0.58);
        color:  #fff;
        position: relative;
        
        &::after {
            top: 100%;
            left: 50%;
            border: solid transparent;
            content: " ";
            height: 0;
            width: 0;
            position: absolute;
            top: -48px;
            pointer-events: none;
            border-color: rgba(136, 183, 213, 0);
            border-top-color: #e1e4e8;
            border-width: 75px;
            margin-left: -75px;
            .md(display, none);
        }
        &__item {
            &::before {
                content: '';
                position: absolute;
                left: 0; top: 0;
                background: url(../img/form_white.png) no-repeat;
                width: 56px;
                height: 56px;
            }
        }
    }
    &_btn {
        text-align: center;
        margin: 35px auto 0;
    }
}