@import "smart-grid.less";
html {
    font-size: 16px;
    .md(font-size, 14px);
    .xs(font-size, 12px);
    .xxs(font-size, 10px);
}
body {
    font-family: "OpenSansRegular", sans-serif;
}
h2.h2 {
    text-transform: uppercase;
    font-size: 2.5rem;
}
p {
    font-family: "OpenSansLight", sans-serif;
    font-size: 1.375rem;
}
a {
    text-decoration: none;
}
.gray_btn {
    display: inline-block;
    padding: 23px 45px;
    box-shadow: 1px 1px 7.4px 2.6px rgba(25, 25, 25, 0.54);
    color:  #fff;
    background-color: rgba(26,28,28,1);
    font-size: 2.25rem;
    text-transform: uppercase;
    
    .xxs(box-shadow, none);
    
    &:hover {
        background-color: rgba(26,28,28,0.7);
        font-size: 2.3rem;
    }
}
.title-afterarrow {
    padding-top: 45px;
}