.intro {
    background: url(../img/intro.jpg) no-repeat;
    background-size: cover;
    background-position: center center;
    text-align: center;

    .logo {
        margin: 4.5rem 0 5.5rem;
    }

    &_title {
        position: relative;
        .col();  
        padding: 35px 20px 40px;
        background-color: rgba(225,228,232,0.7);

        &::before{
            content: '';
            position: absolute;
            bottom: -105px;
            left: 50%; transform: translate(-50%);
            height: 105px;
            width: 205px;
            background: url(../img/after.png) no-repeat;
        }

        h1 {
            font-size: 4.5rem;
            text-transform: uppercase;

            span {
                font-size: 3rem;
                text-transform: none;
                border-bottom: 5px dotted #000;
            }
        }
    }
    &_start {
        margin-top: 19rem;

        &__title {
            .col();
            .size(8);
            .size-md(12);
            font-size: 2.25rem;
            text-align: center;
            span {
                text-transform: uppercase;
            }
        }
        .features {
            .row-float();
            margin: 20px auto 50px;
            text-align: left;

            ul {
                float: right;
            }

            &_item {
                padding: 10px 0;
                padding-left: 70px;
                margin: 30px 0;
                position: relative;

                &::before {
                    content: '';
                    position: absolute;
                    left: 0; top: 0;
                    background: url(../img/form.png) no-repeat;
                    width: 56px;
                    height: 56px;
                }
            } 
        }
        .record {

            &_wrap {
                .row-flex();
                justify-content: center;

                .record_item {
                    box-shadow: 0px 0px 10.66px 2.34px rgba(25, 25, 25, 0.45);
                    width: 181px;
                    height: 181px;
                    border-radius: 50%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    margin: 40px 2rem;
                }
                .time {
                    color:  #000001;
                    font-size: 56.69px;
                    line-height: 58.365px;
                    text-align: center;
                }
            }
            &_btn {
                text-transform: none;
                padding: 28px 25px;
                margin: 30px auto 50px;
            }
        }
    }
}