.final {
    text-align: center;
    padding: 75px 0 50px;
    background: url(../img/final-bg.png) no-repeat;
    background-size: cover;
    background-position: center center;
    color: #fff;
    
    &_title {
        
        font-size: 2.25rem;
        span {
            text-transform: uppercase;
            border-bottom: 1px solid #fff;
        }
    }
    &_ul {
        margin: 30px auto 10px;
        text-align: left;
        padding: 5px 22px;
        background-color: rgba(20,20,20,0.58);
        border: 1px solid #fff;
        float: right;
        .col();
        .size(6);
        .size-sm(10);
        .size-xs(12);
        .sm-block({
            float:none;
            margin: 10px auto 0;
        });
     
        li {
            margin: 30px 0;
            padding-left: 60px;
            position: relative;
            
            &::before {
                content: '';
                font-size: 32px;
                text-align: center;
                position: absolute;
                top: 0px; left: 0;
                border: 4px solid #fff;
                border-radius: 50%;
                width: 45px;
                height: 45px;
            }
        }
        .one {
            &::before {
                content: '1';
            }
        }
        .two {
            &::before {
                content: '2';
            }
        }
        .three {
            &::before {
                content: '3';
            }
        }
        .four {
            &::before {
                content: '4';
                top: -10px;
            }
        }
        .five {
            &::before {
                content: '5';
            }
        }
        .six {
            &::before {
                content: '6';
            }
        }
        .seven {
            &::before {
                content: '7';
            }
        }
        .eight {
            &::before {
                content: '8';
            }
        }
    }
    &_bottom {
        display: block;
        margin: 25px auto 0px;
        font-size: 2rem;
        padding-top: 25px;
        border-top: 4px solid #e3e6ea;
    }
}