body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  overflow: hidden;
}

@font-face {
  font-family: "OpenSansRegular";
  src: url("../fonts/OpenSansRegular/OpenSansRegular.eot");
  src: url("../fonts/OpenSansRegular/OpenSansRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSansRegular/OpenSansRegular.woff") format("woff"), url("../fonts/OpenSansRegular/OpenSansRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "OpenSansLight";
  src: url("../fonts/OpenSansLight/OpenSansLight.eot");
  src: url("../fonts/OpenSansLight/OpenSansLight.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSansLight/OpenSansLight.woff") format("woff"), url("../fonts/OpenSansLight/OpenSansLight.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

html {
  font-size: 16px;
}

body {
  font-family: "OpenSansRegular", sans-serif;
}

h2.h2 {
  text-transform: uppercase;
  font-size: 2.5rem;
}

p {
  font-family: "OpenSansLight", sans-serif;
  font-size: 1.375rem;
}

a {
  text-decoration: none;
}

.gray_btn {
  display: inline-block;
  padding: 23px 45px;
  box-shadow: 1px 1px 7.4px 2.6px rgba(25, 25, 25, 0.54);
  color: #fff;
  background-color: #1a1c1c;
  font-size: 2.25rem;
  text-transform: uppercase;
}

.gray_btn:hover {
  background-color: rgba(26, 28, 28, 0.7);
  font-size: 2.3rem;
}

.title-afterarrow {
  padding-top: 45px;
}

.intro {
  background: url(../img/intro.jpg) no-repeat;
  background-size: cover;
  background-position: center center;
  text-align: center;
}

.intro .logo {
  margin: 4.5rem 0 5.5rem;
}

.intro_title {
  position: relative;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  padding: 35px 20px 40px;
  background-color: rgba(225, 228, 232, 0.7);
}

.intro_title::before {
  content: '';
  position: absolute;
  bottom: -105px;
  left: 50%;
  transform: translate(-50%);
  height: 105px;
  width: 205px;
  background: url(../img/after.png) no-repeat;
}

.intro_title h1 {
  font-size: 4.5rem;
  text-transform: uppercase;
}

.intro_title h1 span {
  font-size: 3rem;
  text-transform: none;
  border-bottom: 5px dotted #000;
}

.intro_start {
  margin-top: 19rem;
}

.intro_start__title {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(66.66666666666667% - 30px);
  font-size: 2.25rem;
  text-align: center;
}

.intro_start__title span {
  text-transform: uppercase;
}

.intro_start .features {
  margin-left: -15px;
  margin-right: -15px;
  margin: 20px auto 50px;
  text-align: left;
}

.intro_start .features::after {
  display: block;
  content: "";
  clear: both;
}

.intro_start .features ul {
  float: right;
}

.intro_start .features_item {
  padding: 10px 0;
  padding-left: 70px;
  margin: 30px 0;
  position: relative;
}

.intro_start .features_item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  background: url(../img/form.png) no-repeat;
  width: 56px;
  height: 56px;
}

.intro_start .record_wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.intro_start .record_wrap .record_item {
  box-shadow: 0px 0px 10.66px 2.34px rgba(25, 25, 25, 0.45);
  width: 181px;
  height: 181px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 40px 2rem;
}

.intro_start .record_wrap .time {
  color: #000001;
  font-size: 56.69px;
  line-height: 58.365px;
  text-align: center;
}

.intro_start .record_btn {
  text-transform: none;
  padding: 28px 25px;
  margin: 30px auto 50px;
}

.result {
  background: url(../img/bg-result.jpg) no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 48px 0 35px;
  text-align: center;
}

.result_wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
  text-align: left;
}

.result_block {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
  padding: 45px 5px;
}

.result_block h3 {
  font-size: 2rem;
  text-align: center;
}

.result_block ul {
  padding: 0 25px;
}

.result_block li {
  padding: 7px 0;
  padding-left: 70px;
  margin: 40px 0;
  position: relative;
}

.result_white {
  background-color: rgba(225, 228, 232, 0.7);
  position: relative;
}

.result_white::after {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  top: -48px;
  pointer-events: none;
  border-color: rgba(136, 183, 213, 0);
  border-top-color: #1a1b1b;
  border-width: 75px;
  margin-left: -75px;
}

.result_white__item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  background: url(../img/form.png) no-repeat;
  width: 56px;
  height: 56px;
}

.result_black {
  background-color: rgba(20, 20, 20, 0.58);
  color: #fff;
  position: relative;
}

.result_black::after {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  top: -48px;
  pointer-events: none;
  border-color: rgba(136, 183, 213, 0);
  border-top-color: #e1e4e8;
  border-width: 75px;
  margin-left: -75px;
}

.result_black__item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  background: url(../img/form_white.png) no-repeat;
  width: 56px;
  height: 56px;
}

.result_btn {
  text-align: center;
  margin: 35px auto 0;
}

.content {
  background: url(../img/content-bg.jpg) no-repeat;
  background-size: cover;
  background-position: center center;
  text-align: center;
}

.content_title {
  display: inline-block;
  margin: 50px auto;
  border-bottom: 5px dotted #000;
}

.content_h3 {
  position: relative;
  font-size: 2.25rem;
  text-transform: uppercase;
  padding: 7px 0;
}

.content_h3::before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(136, 183, 213, 0);
  border-top-color: #1a1b1b;
  border-width: 45px;
  margin-left: -45px;
}

.content_block {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 25px;
  margin-bottom: 70px;
  padding: 32px 25px 35px;
  background-color: #fcfcfc;
}

.content_block__title {
  padding-bottom: 5px;
  font-family: 'OpenSansLight', sans-serif;
  color: #363636;
  font-size: 34.02px;
}

.content_block__text {
  position: relative;
  display: inline-block;
  padding: 15px 0 10px;
  text-align: left;
}

.content_block__text::before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(136, 183, 213, 0);
  border-top-color: #1a1b1b;
  border-width: 45px;
  margin-left: -45px;
}

.content_block__ul {
  display: inline-block;
  margin: 10px 0 30px;
}

.content_block__item {
  text-align: left;
  list-style-type: disc;
}

.content_wrap {
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
  align-items: stretch;
}

.content_item {
  position: relative;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
}

.content_item .content_h3 {
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translate(-50%);
}

.content_item__inside {
  margin-top: 50px;
  padding: 32px 25px 35px;
  background-color: #fcfcfc;
  height: 90%;
}

.content_item__last {
  height: 65%;
}

.price {
  background: url(../img/price-bg.png) no-repeat;
  background-size: cover;
  background-position: center center;
  text-align: center;
  min-height: 840px;
  color: #fff;
  padding-bottom: 25px;
}

.price_title {
  display: inline-block;
  margin: 7rem auto 5rem;
  border-bottom: 5px dotted #fff;
}

.price_wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
  margin-bottom: 90px;
}

.price_check {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(58.333333333333336% - 30px);
}

.price .label {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 15px 0;
  font-size: 3.3rem;
  position: relative;
  padding-left: 40px;
}

.price .label::before {
  left: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  left: 0;
  pointer-events: none;
  border-left-color: #fff;
  border-width: 32px;
  margin-top: -32px;
}

.price .checkbox_text {
  text-align: right;
  margin-left: auto;
}

.price .checkbox {
  display: none;
}

.price i {
  position: relative;
  display: block;
  width: 55px;
  height: 52px;
  background-color: #b4bac1;
  box-shadow: 1px 1px 7.8px 2.2px rgba(25, 25, 25, 0.54);
  margin: 0 50px;
}

.price i::before {
  content: '';
  position: absolute;
  left: 15px;
  top: 8px;
  background: url(../img/check.png) no-repeat;
  width: 34px;
  height: 26px;
  opacity: 0;
  transition: .3s all;
}

.price .checkbox:checked + i::before {
  opacity: 1;
}

.price_p {
  display: inline-block;
  margin: 0 10px 50px;
}

.price_p span {
  font-size: 3.3rem;
  line-height: 1;
}

.price_btn {
  background-color: transparent;
  border: 1px solid #fff;
  padding: 19px 70px;
  text-transform: uppercase;
  font-size: 2.25rem;
  color: #fff;
}

.price_btn:hover {
  background-color: rgba(26, 28, 28, 0.7);
  font-size: 2.3rem;
}

.bonus {
  background-color: #e2e5e9;
  color: #151515;
  text-align: center;
}

.bonus_title {
  position: relative;
  display: inline-block;
  margin: 3rem auto 0;
  padding: 50px 130px;
}

.bonus_title::before {
  content: '';
  background: url(../img/bonus_left.png) no-repeat;
  width: 110px;
  height: 131px;
  position: absolute;
  top: 10px;
  left: 0;
}

.bonus_title::after {
  content: '';
  background: url(../img/bonus_right.png) no-repeat;
  width: 99px;
  height: 123px;
  position: absolute;
  top: 10px;
  right: 0;
}

.bonus_minititle {
  position: relative;
  font-size: 2.25rem;
  padding-top: 90px;
  line-height: 1.5;
}

.bonus_minititle span {
  border-bottom: 1px solid #151515;
}

.bonus_minititle::before {
  content: '';
  background: url(../img/bonus_arrow.png) no-repeat;
  width: 137px;
  height: 70px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.bonus_text {
  margin: 40px 0;
  text-align: left;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

.bonus_text__col-left {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
}

.bonus_text__col-left .bonus_ul {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
}

.bonus_text__col-left .sub_menu li {
  list-style-type: disc;
}

.bonus_text__col-right {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(41.66666666666667% - 30px);
}

.bonus_btn {
  display: block;
  margin: 0 auto 40px;
  width: 100%;
  max-width: 280px;
  text-transform: uppercase;
  padding: 15px 20px;
  border: 1px solid #151515;
  color: #151515;
}

.final {
  text-align: center;
  padding: 75px 0 50px;
  background: url(../img/final-bg.png) no-repeat;
  background-size: cover;
  background-position: center center;
  color: #fff;
}

.final_title {
  font-size: 2.25rem;
}

.final_title span {
  text-transform: uppercase;
  border-bottom: 1px solid #fff;
}

.final_ul {
  margin: 30px auto 10px;
  text-align: left;
  padding: 5px 22px;
  background-color: rgba(20, 20, 20, 0.58);
  border: 1px solid #fff;
  float: right;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
}

.final_ul li {
  margin: 30px 0;
  padding-left: 60px;
  position: relative;
}

.final_ul li::before {
  content: '';
  font-size: 32px;
  text-align: center;
  position: absolute;
  top: 0px;
  left: 0;
  border: 4px solid #fff;
  border-radius: 50%;
  width: 45px;
  height: 45px;
}

.final_ul .one::before {
  content: '1';
}

.final_ul .two::before {
  content: '2';
}

.final_ul .three::before {
  content: '3';
}

.final_ul .four::before {
  content: '4';
  top: -10px;
}

.final_ul .five::before {
  content: '5';
}

.final_ul .six::before {
  content: '6';
}

.final_ul .seven::before {
  content: '7';
}

.final_ul .eight::before {
  content: '8';
}

.final_bottom {
  display: block;
  margin: 25px auto 0px;
  font-size: 2rem;
  padding-top: 25px;
  border-top: 4px solid #e3e6ea;
}

.help {
  background-color: #e2e5e9;
  text-align: center;
}

.help_title {
  margin-top: 3rem;
  border-bottom: 5px dotted #000;
  display: inline-block;
}

.help_block {
  margin: 60px auto;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  align-items: center;
  justify-content: center;
}

.help_block .manager {
  height: 291px;
  width: 291px;
  border-radius: 50%;
  border: 1px solid #fff;
  background-color: #d0d5db;
  margin-right: 25px;
}

.help_block .manager p {
  padding: 5px 0 10px;
}

.help_block .call {
  margin-left: 25px;
  color: #000;
  text-transform: uppercase;
  background-color: #e9ecef;
  border: 1px solid #000;
  padding: 26px 50px;
}

.help_social {
  float: right;
}

.help_social a {
  display: inline-block;
  margin: 0 5px;
  padding: 5px;
}

.help_social .youtube {
  background: url(../img/youtube.png) no-repeat;
  height: 62px;
  width: 62px;
}

.help_social .fb {
  background: url(../img/facebook.png) no-repeat;
  height: 62px;
  width: 62px;
}

.help_social .tw {
  background: url(../img/twitter.png) no-repeat;
  height: 62px;
  width: 62px;
}

.help_social .google {
  background: url(../img/google.png) no-repeat;
  height: 62px;
  width: 62px;
}

.main-footer {
  padding: 80px 0 10px;
  background-color: #e2e5e9;
  text-align: center;
}

.main-footer__text {
  color: #1f1f1f;
  font-size: 17.01px;
}

@media screen and (max-width: 992px) {
  html {
    font-size: 14px;
  }

  .intro_start__title {
    width: calc(100% - 30px);
  }

  .result_block {
    width: calc(66.66666666666667% - 30px);
  }

  .result_black::after {
    display: none;
  }

  .content_item {
    width: calc(66.66666666666667% - 30px);
  }

  .content_item {
    margin: 35px 0;
  }

  .price_check {
    width: calc(66.66666666666667% - 30px);
  }

  .price .label {
    padding-left: 0px;
  }

  .price .label::before {
    display: none;
  }

  .price .checkbox_text {
    text-align: center;
  }

  .bonus_text {
    justify-content: center;
  }

  .bonus_text__col-left {
    width: calc(66.66666666666667% - 30px);
  }

  .bonus_text__col-right {
    width: calc(66.66666666666667% - 30px);
  }

  .bonus_text__col-right {
    margin-top: 10px;
  }
}

@media screen and (max-width: 720px) {
  .result_block {
    width: calc(83.33333333333334% - 30px);
  }

  .content_title {
    border-bottom: none;
  }

  .content_item {
    width: calc(83.33333333333334% - 30px);
  }

  .price_title {
    border-bottom: none;
  }

  .price_check {
    width: calc(100% - 30px);
  }

  .bonus_text__col-left {
    width: calc(83.33333333333334% - 30px);
  }

  .bonus_text__col-right {
    width: calc(83.33333333333334% - 30px);
  }

  .final_ul {
    width: calc(83.33333333333334% - 30px);
  }

  .final_ul {
    float: none;
    margin: 10px auto 0;
  }

  .help_title {
    border-bottom: none;
  }

  .help_block .manager {
    margin-right: 0;
    margin-bottom: 35px;
  }

  .help_block .call {
    margin-left: 0px;
  }
}

@media screen and (max-width: 576px) {
  html {
    font-size: 12px;
  }

  .result_block {
    width: calc(100% - 30px);
  }

  .content_item {
    width: calc(100% - 30px);
  }

  .price_wrap {
    margin-bottom: 30px;
  }

  .price .label {
    flex-direction: column;
  }

  .price .checkbox_text {
    margin: auto;
  }

  .bonus_title::before {
    display: none;
  }

  .bonus_title::after {
    display: none;
  }

  .bonus_minititle {
    padding-top: 0px;
  }

  .bonus_minititle::before {
    display: none;
  }

  .final_ul {
    width: calc(100% - 30px);
  }
}

@media screen and (max-width: 380px) {
  .container {
    padding-left: 5px;
    padding-right: 5px;
  }

  html {
    font-size: 10px;
  }

  .gray_btn {
    box-shadow: none;
  }

  .bonus_text__col-left .sub_menu li {
    list-style-type: none;
  }

  .bonus_btn {
    max-width: 220px;
  }

  .help_block .call {
    padding: 25px 30px;
  }

  .help_social {
    float: none;
  }

  .help_social a {
    margin: 0;
  }
}