.content {
    background: url(../img/content-bg.jpg) no-repeat;
    background-size: cover;
    background-position: center center;
    text-align: center;

    &_title {
        display: inline-block;
        margin: 50px auto;
        border-bottom: 5px dotted #000;
        
        .sm(border-bottom, none);
    }
    &_h3 {
        position: relative;
        font-size: 2.25rem;
        text-transform: uppercase;
        padding: 7px 0;
        
        &::before {
            top: 100%;
            left: 50%;
            border: solid transparent;
            content: " ";
            height: 0;
            width: 0;
            position: absolute;
            pointer-events: none;
            border-color: rgba(136, 183, 213, 0);
            border-top-color: #1a1b1b;
            border-width: 45px;
            margin-left: -45px;
        }
    }
    &_block {
        .col();
        margin-top: 25px;
        margin-bottom: 70px;
        padding: 32px 25px 35px;
        background-color: #fcfcfc;
     
        &__title {
            padding-bottom: 5px;
            font-family: 'OpenSansLight',sans-serif;
            color:  #363636;
            font-size: 34.02px;
        }
        &__text {
            position: relative;
            display: inline-block;
            padding: 15px 0 10px;
            text-align: left;
        
            &::before {
                top: 100%;
                left: 50%;
                border: solid transparent;
                content: " ";
                height: 0;
                width: 0;
                position: absolute;
                pointer-events: none;
                border-color: rgba(136, 183, 213, 0);
                border-top-color: #1a1b1b;
                border-width: 45px;
                margin-left: -45px;
            }
        }
        &__ul {
            display: inline-block;
            margin: 10px 0 30px;
        }
        &__item {
            text-align: left;
            list-style-type: disc;
        }
    }
    &_wrap {
        margin-top: 15px;
        .row-flex();
        justify-content: center;
        align-items: stretch;
    }
    &_item {
        position: relative;
        .col();
        .size(6);
        .size-md(8);
        .size-sm(10);
        .size-xs(12);
        
        .md(margin, 35px 0);
        
        .content_h3 {
            position: absolute;
            top: -40px; left: 50%;
            transform: translate(-50%);
        }
    
        &__inside {
            margin-top: 50px;
            padding: 32px 25px 35px;
            background-color: #fcfcfc;
            height: 90%;
        }
    }
   &_item__last {
        height: 65%;
    }
        
}
