.bonus {
    background-color: #e2e5e9;
    color:  #151515;
    text-align: center;
    
    &_title {
        position: relative;
        display: inline-block;
        margin: 3rem auto 0;
        padding: 50px 130px;
        
        &::before {
            content: '';
            background: url(../img/bonus_left.png) no-repeat;
            width: 110px;
            height: 131px;
            position: absolute;
            top: 10px; left: 0;
            
            .xs(display, none);
        }
        &::after {
            content: '';
            background: url(../img/bonus_right.png) no-repeat;
            width: 99px;
            height: 123px;
            position: absolute;
            top: 10px; right: 0;
            
            .xs(display, none);
        }
    }
    &_minititle {
        position: relative;
        font-size: 2.25rem;
        padding-top: 90px;
        line-height: 1.5;
        .xs(padding-top, 0px);
        
        span {
            border-bottom: 1px solid #151515;
        }
        
        &::before {
            content: '';
            background: url(../img/bonus_arrow.png) no-repeat;
            width: 137px;
            height: 70px;
            position: absolute;
            top: 0; left: 50%;
            transform: translate(-50%);
            
            .xs(display, none);
        }
    }
    &_text {
        margin: 40px 0;
        text-align: left;
        .row-flex();
        justify-content: space-between;
        .md(justify-content, center);
        
        &__col-left {
            .col();
            .size(6);
            .size-md(8);
            .size-sm(10);
            
            .bonus_ul {
                display: flex;
                justify-content: space-around;
                margin-top: 10px;
            } 
            .sub_menu {   
                
                li {
                    list-style-type: disc;
                    .xxs(list-style-type,none);
                }
            }
        }
        &__col-right {
            .col();
            .size(5);
            .size-md(8);
            .size-sm(10);
            .md(margin-top, 10px);
            
        }
    }
    &_btn {
        display: block;
        margin: 0 auto 40px;
        width: 100%;
        max-width: 280px;
        text-transform: uppercase;
        padding: 15px 20px;
        border: 1px solid #151515;
        color: #151515;
        .xxs(max-width,220px);
    }
}