.help {
    background-color: #e2e5e9;
    text-align: center;
    
    &_title {
        margin-top: 3rem;
        border-bottom: 5px dotted #000;
        display: inline-block;
        .sm(border-bottom, none);
    }
    &_block {
        margin: 60px auto;
        .row-flex();
        align-items: center;
        justify-content: center;
        
        .manager {
            height: 291px;
            width: 291px;
            border-radius: 50%;
            border: 1px solid #fff;
            background-color: #d0d5db;
            margin-right: 25px;
            .sm-block({
                margin-right: 0;
                margin-bottom: 35px;
            });
            p {
                padding: 5px 0 10px;
            }
        }
        .call {
            margin-left: 25px;
            color: #000;
            text-transform: uppercase;
            background-color: #e9ecef;
            border: 1px solid #000;
            padding: 26px 50px;
            .sm(margin-left, 0px);
            .xxs-block({
               padding: 25px 30px; 
            });
        }
    }
    &_social {
        float: right;
        .xxs(float, none);
        
        a {
            display: inline-block;
            margin: 0 5px;
            padding: 5px;
            .xxs-block({
               margin: 0; 
            });
        }
        .youtube {
            background: url(../img/youtube.png) no-repeat;
            height: 62px;
            width: 62px;
        }
        .fb {
            background: url(../img/facebook.png) no-repeat;
            height: 62px;
            width: 62px;
        }
        .tw {
            background: url(../img/twitter.png) no-repeat;
            height: 62px;
            width: 62px;
        }
        .google {
            background: url(../img/google.png) no-repeat;
            height: 62px;
            width: 62px;
        }
    }
}
.main-footer {
    padding: 80px 0 10px;
    background-color: #e2e5e9;
    text-align: center;
    
    &__text {
        color:  #1f1f1f;
        font-size: 17.01px;
    }
}