.price {
    background: url(../img/price-bg.png) no-repeat;
    background-size: cover;
    background-position: center center;
    text-align: center;
    min-height: 840px;
    color: #fff;
    padding-bottom: 25px;

    &_title {
        display: inline-block;
        margin: 7rem auto 5rem;
        border-bottom: 5px dotted #fff;
        .sm(border-bottom, none);
    }
    &_wrap {
        .row-flex();
        justify-content: center;
        margin-bottom: 90px;
        .xs-block({
            margin-bottom: 30px;
        })
    }
    &_check {
        .col();
        .size(7);
        .size-md(8);
        .size-sm(12);
    }
    .label {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 15px 0;
        font-size: 3.3rem;
        position: relative;
        padding-left: 40px;
        .md(padding-left, 0px);
        .xs-block({
            flex-direction: column;
        });
        
        &::before {
            left: 100%;
            top: 50%;
            border: solid transparent;
            content: " ";
            height: 0;
            width: 0;
            position: absolute;
            left: 0;
            pointer-events: none;
            border-left-color: #fff;
            border-width: 32px;
            margin-top: -32px;
            .md(display, none);
        }
    }
    .checkbox_text {
        text-align: right;
        margin-left: auto;
        .md(text-align, center);
        .xs-block({
            margin: auto;
        })
    }
    .checkbox {
        display: none;
    }
    i {
        position: relative;
        display: block;
        width: 55px;
        height: 52px;
        background-color: #b4bac1;
        box-shadow: 1px 1px 7.8px 2.2px rgba(25, 25, 25, 0.54);
        margin: 0 50px;
        
        &::before {
            content: '';
            position: absolute;
            left: 15px; top: 8px;
            background: url(../img/check.png) no-repeat;
            width: 34px;
            height: 26px;
            opacity: 0;
            transition: .3s all;
        }
    }
    .checkbox:checked+i::before {
        opacity: 1;
    }
    &_p {
        display: inline-block;
        margin: 0 10px 50px;
        
        span {
            font-size: 3.3rem;
            line-height: 1;
        }
    }
    &_btn {
        background-color: transparent;
        border: 1px solid #fff;
        padding: 19px 70px;
        text-transform: uppercase;
        font-size: 2.25rem;
        color: #fff;
        &:hover {
            background-color: rgba(26,28,28,0.7);
            font-size: 2.3rem;
        }
    }
}